/* App */

body{
  background-color: rgb(159, 192, 235);
  margin: 0 10%;
}

#navBar{
  text-align: center;  
  min-width: 700px;
}
.navItem{
  display:inline;                                                                                
}
h1{
  font-size: 60px;
  text-shadow: 3px 3px 5px #314cc7;
}
#homeIMG{
  border: 4px solid black;
  padding: 5px;
  background-color: rgb(183, 236, 236);
  border-radius: 50%;
  margin-right: 55px;
  position: relative;
  top: 10px;
  width: 55px;
  cursor: pointer;
}
#cartIMG{
  border-left: 4px solid black;
  border-right: 4px solid black;
  border-bottom: 4px solid black;
  padding: 5px;
  background-color: rgb(183, 236, 236);
  border-radius: 50%;
  margin-left: 55px;
  width: 55px;
  position: relative;
  top: 10px;
  cursor: pointer;
}
#productsNum{
  font-size: 30px;
  font-weight: bold;
  position: relative;
  right: 48px;
  bottom: 48px;
}

#container{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 650px;
  border-bottom:6px solid rgb(156, 160, 173);
  border-top:4px solid cyan;
  background-color: rgb(116, 126, 221);
  min-height: 400px;
  margin-bottom: 10px;
}
   
  /* Home Page */

input{
  width: 30%;
  min-width: 150px;
  height: 35px;  
  margin-left: 20px;
  font-size: 20px;
  border:2px solid green;
}
.add{
  font-size: 25px;
  margin-left: 20px;
  width: 40px;
  height: 40px;
  background-color: rgb(116, 126, 221);
  border:2px solid cyan;
  cursor: pointer;
}
.add:hover{
  background-color: rgb(59, 64, 112);

}
h2{
  font-size: 30px;
}

/* Cart */

#cartContainer{
  height: 400px;
}
#cartIcon{
  border: 4px solid rgb(113, 187, 236);
  border-radius: 30%;
  width: 40px;
  height: 40px;
  margin-left: 50px;
  margin-top: -8px;
  position: absolute;
}
#totalDiv{
 margin-top: 20px;
 background-color: rgba(25, 0, 255, 0.884); 
 border:4px solid rgb(143, 174, 236);
 width: 80%; 
 min-width: 620px;
 height: 80px;
}
#total{
  text-align: center;
  font-size: 26px;
  margin-top: 20px;  
}
#buyBTN{
  margin-bottom: 20px;
  font-size: 30px;
  background-color: rgb(84, 182, 238);
  border: 4px solid rgb(64, 38, 238);
  cursor: pointer
}
#buyBTN:hover{
  background-color: rgb(56, 120, 240);
  border: 4px solid rgb(38, 6, 248);
}

/* Product */

.itemDiv{
  border:4px solid cornflowerblue;
  width: 80%; 
  min-width: 620px;
  height: 80px;
  background-color: rgb(45, 3, 233);
}
.itemDiv:hover{
  border:4px solid rgb(0, 180, 75);
  background-color: rgb(31, 0, 170);
}
.Columns{
  display: inline-block;
  margin-left: 5%;
  width: 35%;
  min-width: 250px; 
}
h3{
  font-size:20px;
  margin-left: 3%;
}
#h3{
  margin-left: 3%;
  font-size: 22px;
}
#deletBTN{
  color:red;
  margin-top: 20px;
  font-size: 30px;
  width: 40px;
  cursor: pointer;
}
#deletBTN:hover{
  background-color:lightsteelblue;
  border: 2px solid rgb(116, 126, 221);
}

/* Mobile */

@media (max-width: 600px) {
  body{
    margin: 0 5%;
  }
  #navBar{
    text-align: center;  
    min-width: 530px;
    margin-bottom: 0px;
  }
  .navItem{
    display:inline;
  }
  #productsNum{
    font-size: 30px;
    font-weight: bold;
    position: relative;
    right: 48px;
    bottom: 48px;
    overflow: auto;
  }
  h1{
    font-size: 50px;
    text-shadow: 3px 3px 5px #314cc7;
  }
  #homeIMG{
    padding: 5px;
    border-radius: 50%;
    margin-right: 20px;
    position: relative;
    top: 10px;
    width: 55px;
    cursor: pointer;
  }
  #cartIMG{
    padding: 5px;
    background-color: rgb(183, 236, 236);
    border-radius: 50%;
    margin-left: 20px;
    width: 55px;
    position: relative;
    top: 10px;
    cursor: pointer;
  }
  #container{
    min-width: 400px;
    width: 500px;
    min-height: 560px;
  }
  .itemDiv{
    border:4px solid cornflowerblue;
    width: 80% ;
    height: 140px;
    min-width: 50%;
  }
  input{
    display: block;
    width: 60%;
    margin-left: 60px;
    font-size: 20px;
  }
  .add{
    position: relative;
    left: 140px;
    bottom: 70px;
    font-size: 40px;
    width: 60px;
    height: 60px;
  }
  #h3{
    text-align: center;
    font-size: 22px;
    margin-top: 5px;
  }
  h2{
    margin: 8px 0px;
  }
  /* Cart */

  #totalDiv{
    width: 80% ;
    height: 100px;
    min-width: 50%;
   }
  #total{
    text-align: center;
    font-size: 30px;
    margin-top: 28px;
    color:rgb(0, 0, 46)  
  }

  /* Product */
  .Columns{
    display: block;
    min-width: 300px; 
  }
  h3{
    font-size: 24px;
  }
  #deletBTN{
    position: relative;
    left:320px;
    bottom: 120px;
    color:red;
    margin-top: 20px;
    font-size: 40px;
    width: 60px;
    height: 60px;
    cursor: pointer;
  }
  #deletBTN:hover{
    background-color:lightsteelblue;
    border: 2px solid rgb(116, 126, 221);
  }
}